.sidebar {
  width: 100%;
  background: var(--clr-text-secondary);
}

.sidebar-container {
  position: fixed;
  height: 100vh;
  width: 240px;
  min-width: 240px;
  float: left;
  font-family: "Open Sans", sans-serif;
  z-index: 1;
}

.sidebar-container img {
  padding-top: 4rem;
  padding-bottom: 2rem;
  display: block;
  margin: 0 auto;
  width: 80%;
}

.sidebar-links {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.sidebar-links li {
  list-style-type: none;
  width: 100%;
  min-height: 42px;
  height: 5vh;
}

.sidebar-links li:hover {
  cursor: pointer;
  background-color: var(--clr-primary-blue);
  color: var(--clr-text-secondary);
}

.sidebar-link {
  padding-left: 1.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  text-decoration: none;
  height: 100%;
  font-size: 0.8rem;
  font-weight: 700;
  text-transform: uppercase;
}

.sidebar-link-active {
  color: white;
  background-color: rgb(124, 126, 132, 0.5);
}

.sidebar-icon {
  padding-right: 1rem;
  font-size: 1.4rem;
  min-width: 40px;
}

.sidebar-link:hover {
  color: var(--clr-text-secondary);
}

.toggle-button {
  position: absolute;
  right: 2rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
  border: none;
  background: none;
  cursor: pointer;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  border-radius: 10px;
}

.sidebar-signout {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.4);
}

.sidebar-signout p {
  padding-left: 2rem;
  font-family: inherit;
  font-size: 1rem;
}

.sidebar-links button {
  border: none;
  background: none;
  cursor: pointer;
  width: 100%;
}

.sidebar-signout button {
  padding: 1rem;
}

.sidebar-signout button:hover {
  background-color: var(--clr-primary-blue);
  color: var(--clr-text-secondary);
}

.sidebar-signout .sidebar-icon {
  padding: 0;
}

@media (max-width: 900px) {
  .sidebar-container {
    width: 100vw;
    height: 4rem;
    max-width: 1400px;
    display: flex;
    align-items: center;
  }

  .sidebar-container img {
    margin: 0 auto;
    padding: 0;
    height: 70%;
  }

  .toggle-button {
    display: flex;
    color: black;
  }

  .sidebar-links {
    display: none;
    width: 100%;
    position: fixed;
    top: 4rem;
  }

  .sidebar-signout {
    position: relative;
  }

  .sidebar-links ul {
    width: 100%;
    flex-direction: column;
  }

  .sidebar-link {
    text-align: center;
  }

  .sidebar-links.active {
    display: flex;
  }
}
